body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.App {
  background-color: #eeeeee;
}

header {
  position: fixed;
  z-index: 1;
  top: 0;
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  background-color: #06113C;
  color: #eeeeee;
  font-weight: bold;
  font-size: xx-large;
  box-shadow: 0px 1px 6px 2px rgba(0,0,0, 0.4);
}

@media (max-width: 600px) {
  header {
    flex-direction: column;
    justify-content: center;
  }

  #logo-container {
    justify-content: center;
    padding-top: 20px;
  }

  #navbarButton-container {
    justify-content: center;
    margin-right: 0 !important;
  }
}

#logo-container {
  display: flex;
  align-items: center;
  margin: 10px 40px;
  width: 50vw;
}

#button-index {
  border: none;
  background-color: inherit;
  color: inherit;
  font-size: inherit;
  font-weight: inherit;
  cursor: pointer;
  transition: 0.3s;
}

#button-index:hover{
  color: #FF8C32;
}

#navbarButton-container {
  display: flex;
  justify-content: space-evenly;
  align-items: stretch;
  margin-right: 20px;
}

#content-container {
  position: absolute;
  min-height: 100vh;
  width: 100vw;
  padding: 150px 0;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
}
.button-container {
  flex: 1 1;
  min-width: 120px;
  height: 100px;
  border: none;
  background-color: inherit;
  color: inherit;
  font-size: large;
  font-weight: inherit;
  cursor: pointer;
  transition: 0.3s;
}

.button-container:hover {
  border: 1px solid #FF8C32;
  color: #FF8C32
}

@media (max-width: 600px) {
  .button-container {
    height: 50px;
  }
}
.presentation-container {
  background-color: inherit;
  margin: auto;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.presentation-text {
  overflow: hidden;
  white-space: nowrap;
  margin: 0;
  font-size: 6vw;
  font-weight: bolder;
  
  background-image: url(/static/media/space.d07776a2.jpg);

  background-clip: text;
  -webkit-background-clip: text;
  -moz-background-clip: text;

  color: transparent;  
}

.aboutMe-container {
  margin: 0 100px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
}

.img-container {
  overflow: hidden;
  width: 400px;
  height: 500px;
  border: 1px solid #06113C;
  box-shadow: 0px 1px 6px 2px rgba(0,0,0, 0.4);
}

.aboutMe-image {
  width: auto !important;
  height: 100% !important;
  color: white;
  background-color: #06113C;
}

.information-container {
  width: 50vw;
  padding-left: 20px;
  font-size: large;
  color: #06113C;
  line-height: 1.8;
}
.myprojects-container {
  width: 80vw;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}

h2 {
  margin: 15px 30px;
  text-shadow: 0px 1px 3px #ff8b3273;
}

.folders-container {
  margin: 20px 0;
}

.foldersButton {
  margin: 0 20px;
  padding: 20px;
  font-size: large;
  color: #06113C;
  background-color: transparent;
  border: 2px solid transparent;
  border-radius: 50px;
  cursor: pointer;
  transition: 0.3s;
}

.foldersButton:hover {
  border: 2px solid #FF8C32;
  color: #FF8C32;
}

i {
  margin-right: 10px;
}

.projects-view {
  width: 80vw;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  flex-wrap: wrap;
}

.projectsView-container {
  width: 400px;
  height: 400px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 20px;
  color: #06113C;
  background-color: #eeeeee;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  
}

a {
  width: 100%;
  height: 100%;
  color: #06113C;
  border: 2px solid #06113C;
  border-radius: 5px;
  text-decoration: none;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  transition: 0.3s;
}

img {
  width: 100%;
  height: 300px;
}

.project-title {
  margin: auto;
  text-align: center;
  font-size: large;
  font-weight: bold;
  color: inherit;
}

a:hover {
  border-color: #FF8C32;
  color: #FF8C32;
  cursor: pointer;
}
